import { Injectable } from '@angular/core';
import { LoadingController, LoadingOptions, ToastController, ToastOptions } from 'ionic-angular';
const DEFAULT_ERROR_MESSAGE = 'An error occurred.';
@Injectable()
export class ToastService {
  constructor(
    public toastCtrl: ToastController) {}

  show(message) {
    let toastOptions: ToastOptions = {};
    toastOptions.message = message;
    toastOptions.duration = 3000;
    const toast = this.toastCtrl.create(toastOptions);
    toast.present();
  }

  onSuccess(promise, message) {
    let that = this;

    promise.then(function(err) {
      let toastOptions: ToastOptions = {};
      toastOptions.message = message;
      toastOptions.duration = 3000;

      const toast = this.toastCtrl.create(toastOptions);
      toast.present();
    });
  }

  onFailure(promise: Promise<any>, message?: string) {
    let that = this;

    message = message || DEFAULT_ERROR_MESSAGE;

    promise.catch(function(err) {
      console.error(err);

      let toastOptions: ToastOptions = {};
      toastOptions.message = message;
      toastOptions.duration = 3000;

      const toast = this.toastCtrl.create(toastOptions);
      toast.present();
    });
  }
}



